export default "import Button from 'component/Button'\nimport Popover from 'component/Popover'\nimport styles from './index.less'\n\n\nconst PopoverBaseView = () => {\n\n    return (\n        <div className={styles.container}>\n            <div className={styles.item}>\n                <Popover\n                    content=\"上方左对齐的一段文字\"\n                    placeholder=\"TL\"\n                ><Button>上方左角对齐</Button>\n                </Popover>\n                <Popover\n                    content=\"上方居中的一段文字\"\n                    placeholder=\"TOP\"\n                >\n                    <Button>上方居中</Button>\n                </Popover>\n                <Popover\n                    content=\"上方右上角对齐的一段文字\"\n                    placeholder=\"TR\"\n                >\n                    <Button>上方右上角对齐</Button>\n                </Popover>\n            </div>\n            <div className={styles.item}>\n                <Popover\n                    content=\"左上角对齐的一段文字\"\n                    placeholder=\"LT\"\n                >\n                    <Button>左上角对齐</Button>\n                </Popover>\n                <Popover\n                    content=\"右上边对齐的一段文字\"\n                    placeholder=\"RT\"\n                >\n                    <Button>右上边对齐</Button>\n                </Popover>\n            </div>\n            <div className={styles.item}>\n                <Popover\n                    content=\"左边居中齐的一段文字\"\n                    placeholder=\"L\"\n                >\n                    <Button>左边居中</Button>\n                </Popover>\n                <Popover\n                    content=\"右边居中的一段文字\"\n                    placeholder=\"R\"\n                >\n                    <Button>右边居中</Button>\n                </Popover>\n            </div>\n            <div className={styles.item}>\n                <Popover\n                    content=\"左边下对齐的一段文字\"\n                    placeholder=\"LB\"\n                >\n                    <Button>左边下对齐</Button>\n                </Popover>\n                <Popover\n                    content=\"右边下对齐的一段文字\"\n                    placeholder=\"RB\"\n                >\n                    <Button>右边下对齐</Button>\n                </Popover>\n            </div>\n            <div className={styles.item}>\n                <Popover\n                    content=\"下边左对齐的一段文字\"\n                    placeholder=\"BL\"\n                >\n                    <Button>下边左对齐</Button>\n                </Popover>\n                <Popover\n                    content=\"下边居中的一段文字\"\n                    placeholder=\"BOTTOM\"\n                >\n                    <Button>下边居中</Button>\n                </Popover>\n                <Popover\n                    content=\"下边有对齐的一段文字\"\n                    placeholder=\"BR\"\n                >\n                    <Button>下边有对齐</Button>\n                </Popover>\n            </div>\n        </div>\n    )\n}\n\nexport default PopoverBaseView;";