export default "import styles from './index.less';\nimport Button from 'component/Button'\nimport Popover from 'component/Popover'\nimport { useState } from 'react';\n\nconst PopoverTypeView = () => {\n\n    const [visible, setVisible] = useState(false);\n\n    return (\n        <div className={styles.container}>\n            <Popover\n                content=\"悬浮触发popover\"\n                placeholder=\"TOP\"\n            >\n                <Button>悬浮触发</Button>\n            </Popover>\n            <Popover\n                content=\"点击触发popover\"\n                placeholder=\"TOP\"\n                trigged=\"click\"\n\n            >\n                <Button>点击触发</Button>\n            </Popover>\n            <Popover\n                placeholder=\"TOP\"\n                visible={visible}\n                trigged=\"popover\"\n                onVisibleChange={() => {setVisible(false)}}\n                content={<div>\n                    <div>受控触发按钮</div>\n                    <Button onClick={() => { setVisible(false) }} >知道了</Button>\n                </div>}>\n                <Button onClick={() => setVisible(true)}>受控触发按钮</Button>\n            </Popover>\n\n        </div>\n    )\n}\n\nexport default PopoverTypeView;";