import React, { FunctionComponentElement, ReactElement, useState } from 'react';
import classnames from 'classnames';
// import { IconEye } from '@workec/ec-icon';
import styles from './index.less';
import FlexContainer from '../FlexContainer';
import FlexItem from '../FlexItem';
import Button from '../Button';
import Text from '../Text';
import Code from '../Code';

export interface Props{
    title:string,
    demo:ReactElement,
    codes:{name:string, type:'ts'|'less'|'tsx', code:string}[],
}

const Demo = (props:Props):FunctionComponentElement<Props> => {
    const { demo, codes, title } = props;
    const [showIndex, setShowIndex] = useState(-1);
    return (
        <section>
            <Text level="h2" className={styles.title}>{title}</Text>
            <div className={styles.demo}>
                {demo}
            </div>
            <FlexContainer flex="horizontal-center" className={styles.demoAction}>
                <FlexItem grow />
                <>
                    {
                        codes.map((item, index) => (
                            <Button
                                key={item.name}
                                type="link"
                                onClick={() => {
                                    if (showIndex === index) {
                                        setShowIndex(-1);
                                    } else {
                                        setShowIndex(index);
                                    }
                                }}
                                // prefix={<IconEye className={styles.icon} />}
                                className={styles.action}
                            >
                                {showIndex === index ? '收起' : '查看'}
                                {item.name}
                            </Button>
                        ))
                    }
                </>
            </FlexContainer>
            {codes[showIndex] ? <Code className={styles.code} code={codes[showIndex].code} type={codes[showIndex].type} /> : null}
        </section>
    );
};

export default Demo;
