import classNames from 'classnames';
import { useMemo, useState, useRef, useEffect } from 'react'
import { createPortal } from 'react-dom';
import { CSSTransition } from 'react-transition-group'
// import { useRef } from 'react-virtualized/node_modules/@types/react';
import styles from './index.less'
import { calculateContentPosition, calculateTrianglePosition } from './util';
interface Props {
    content: string | JSX.Element;
    placeholder: 'TOP' | 'TR' | 'TL' | 'BOTTOM' | 'BL' | 'BR' | 'LT' | 'L' | 'LB' | 'R' | 'RT' | 'RB';
    trigged?: string;
    children: JSX.Element;
    visible?: boolean;
    onVisibleChange?: ()=>void;
    isArrow?: boolean;
    overlayClass?: string;
}
const Popover = (props: Props) => {

    const { 
        children, content, placeholder, trigged = "hover", 
        visible, onVisibleChange,
        isArrow=true,
        overlayClass,
    } = props;
    const [show, setShow] = useState(false);
    const triggedRef = useRef(null) as any;
    const contentRef = useRef(null) as any;
    const [contentStye, setContentStyle] = useState() as any;
    const [triangleStyle, setTriangleStyle] = useState() as any;
    // console.log(show);

    useEffect(() => {
        if (visible !== undefined) {
            setShow(visible)
        } 
    }, [visible])
    // 计算弹窗的位置
    useEffect(() => {
        // effect   
        if (triggedRef.current && contentRef.current && show) {
            // 获取滚动距离
            const scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
            // 计算内容区域的位置
            const resContentCss = calculateContentPosition(
                placeholder,
                contentRef.current.getBoundingClientRect(),
                triggedRef.current.getBoundingClientRect(),
                scrollTop,
                isArrow,
            )
            setContentStyle(resContentCss);
            // 计算三角形的位置
            const resTriangleCss = calculateTrianglePosition(
                placeholder,
                triggedRef.current.getBoundingClientRect(),
                scrollTop,
            )
            setTriangleStyle(resTriangleCss)
        }
        return () => {
            // cleanup
        }
    }, [triggedRef, contentRef, show])

    // 打击其他元素关闭
    useEffect(() => {
        const handle = (e: any) => {
            // contentRef.current.     
            console.log(triggedRef.current);
            console.log(e.target);
            
                   
            if (contentRef.current && triggedRef.current) {
                if (
                    e.target == triggedRef.current ||
                    e.target == contentRef.current ||
                    contentRef.current.contains(e.target) ||
                    triggedRef.current.contains(e.target)
                ) {
                    // setShow(false)
                    console.log('0iiii');
                    
                } else {
                    setShow(false);
                    if(onVisibleChange) {
                        onVisibleChange();
                    }
                }
            }


        }
        document.body.addEventListener('click', handle)

        return () => {
            document.body.removeEventListener('click', handle)
        }
    }, [show])

    const handleClick = () => {
        if (trigged === 'click') {
            setShow(!show)
        }
    }
    const handleOver = () => {
        if (trigged === 'hover') {
            setShow(true)
        }
    }

    const handleLeave = () => {
        if (trigged === 'hover') {
            setShow(false)
        }
    }

    const renderCss = useMemo(() => {

        return createPortal(<CSSTransition
            in={show}
            timeout={200}
            classNames="fade"
            unmountOnExit
            onEnter={(el: any) => {
                // console.log(el); 
            }}
            onExit={(el) => {
                // console.log(el);
            }}
        // appear

        >
            <div>
                <div className={classNames( overlayClass,styles.popoverContainer)} style={contentStye} ref={contentRef}>
                    <div className={styles.popoverContent} >
                        {content}
                    </div>

                </div>
                {
                    isArrow ? <div className={styles.arrow} style={triangleStyle}></div>: null
                }
                
            </div>

        </CSSTransition>, document.body)
    }, [contentStye, show, triangleStyle, content])

    return <>
        <span
            style={{ display: 'inline-block' }}
            onClick={handleClick}
            onMouseOver={handleOver}
            onMouseLeave={handleLeave}
            ref={triggedRef}
        >{children}</span>
        {renderCss}


    </>
}

export default Popover;
