import styles from './index.less';
import Button from 'component/Button'
import Popover from 'component/Popover'
import { useState } from 'react';

const PopoverTypeView = () => {

    const [visible, setVisible] = useState(false);

    return (
        <div className={styles.container}>
            <Popover
                content="悬浮触发popover"
                placeholder="TOP"
            >
                <Button>悬浮触发</Button>
            </Popover>
            <Popover
                content="点击触发popover"
                placeholder="TOP"
                trigged="click"

            >
                <Button>点击触发</Button>
            </Popover>
            <Popover
                placeholder="TOP"
                visible={visible}
                trigged="popover"
                onVisibleChange={() => {setVisible(false)}}
                content={<div>
                    <div>受控触发按钮</div>
                    <Button onClick={() => { setVisible(false) }} >知道了</Button>
                </div>}>
                <Button onClick={() => setVisible(true)}>受控触发按钮</Button>
            </Popover>

        </div>
    )
}

export default PopoverTypeView;