

export const calculateContentPosition = (
    type: string,
    content: any,
    trigged: any,
    scrollTop: number,
    isArrow: boolean,
) => {
    // console.log(trigged.height, scrollTop);
    // console.log(content.height, trigged.top + scrollTop);

    switch (type) {
        case 'TOP':
            const left = trigged.left - content.width / 2 + trigged.width / 2
            const top = trigged.top - content.height - (isArrow ? 10 : 0)
            return {
                top: top < 0 ? 68 : top,
                left: left < 0 ? 0 : left
            }
        case 'TR': {
            const top = trigged.top - content.height - 10
            const left = trigged.left + (trigged.width - content.width);

            return {
                top,
                left,
            }
        }

        case 'TL': {
            const top = trigged.top - content.height - 10
            const left = trigged.left

            return {
                top,
                left,
            }

        }
        case 'BOTTOM': {
            const left = trigged.left - content.width / 2 + trigged.width / 2;
            const top = trigged.top + trigged.height + (isArrow ? 10 : 2);
            return {
                top,
                left,
            }
        }

        case 'BL': {
            const top = trigged.top + trigged.height + (isArrow ? 10 : 2);
            const left = trigged.left;
            return {
                left,
                top,
            }
        }

        case 'BR': {
            const top = trigged.top + trigged.height + 10;
            const left = trigged.left + trigged.width - content.width;
            return {
                top,
                left
            }
        }


        case 'LT': {
            const top = trigged.top - 4;
            const left = trigged.left - content.width - 12;
            return {
                top,
                left,
            }
        }

        case 'L': {
            const top = trigged.top + trigged.height / 2 - content.height / 2;
            const left = trigged.left - 10 - content.width - 2;
            return {
                top,
                left
            }
        }

        case 'LB': {
            const top = trigged.top + trigged.height - content.height + 4;
            const left = trigged.left - 10 - content.width - 2;
            return {
                top,
                left
            }
        }

        case 'R': {
            const top = trigged.top + trigged.height / 2 - content.height / 2;
            const left = trigged.left + trigged.width + 10;
            console.log(left,'rrr', trigged.left,);
            
            return {
                top,
                left,
            }
        }

        case 'RT': {
            const top = trigged.top - 4;
            const left = trigged.left + trigged.width + 10;
            return {
                top,
                left,
            }
        }

        case 'RB': {
            const top = trigged.top + trigged.height - content.height + 4;
            const left = trigged.left + trigged.width + 10;
            return {
                top,
                left,
            }
        }

    }
}

export const calculateTrianglePosition = (
    type: string,
    trigged: any,
    scrollTop: number
) => {
    let triangle = Math.sqrt(10 * 10 + 10 * 10);
    switch (type) {
        case 'TOP':
            return {
                top: trigged.top + scrollTop - triangle - 2 ,
                left: trigged.left + trigged.width / 2 - triangle / 2 - 4,
            }
        case 'TR':
            return {
                top: trigged.top + scrollTop - triangle - 2 ,
                left: trigged.left + trigged.width / 2 - triangle / 2 - 4,
            }
        case 'TL':
            return {
                top: trigged.top + scrollTop - triangle - 2 ,
                left: trigged.left + trigged.width / 2 - triangle / 2 - 4,
            }
        case 'BOTTOM':
            return {
                left: trigged.left + trigged.width / 2 - triangle / 2,
                top: trigged.top + trigged.height + triangle / 2 - 10 / 2 + 2 ,
                transform: 'rotate(225deg)'
            }

        case 'BL':
            return {
                top: trigged.top + trigged.height + triangle / 2 - 10 / 2 + 2 ,
                left: trigged.left + trigged.width / 2 - 2,
                transform: 'rotate(225deg)'
            }
        case 'BR':
            return {
                top: trigged.top + trigged.height + triangle / 2 - 10 / 2 +2,
                left: trigged.left + trigged.width / 2 - 2,
                transform: 'rotate(225deg)'
            }


        case 'LT':
            return {
                left: trigged.left - triangle - 3,
                top: trigged.top + trigged.height / 2 - triangle / 2,
                transform: 'rotate(-45deg)'
            }
        case 'L': {
            return {
                left: trigged.left - triangle - 3,
                top: trigged.top + trigged.height / 2 - triangle / 2,
                transform: 'rotate(-45deg)'
            }
        }
        case 'LB': {
            return {
                left: trigged.left - triangle - 3,
                top: trigged.top + trigged.height / 2 - triangle / 2,
                transform: 'rotate(-45deg)'
            }
        }

        case 'R': {
            return {
                top: trigged.top + trigged.height / 2 - triangle / 2,
                left: trigged.left + trigged.width + triangle / 2 - 10 / 2 + 3,
                transform: 'rotate(135deg)'
            }
        }

        case 'RT': {
            return {
                top: trigged.top + trigged.height / 2 - triangle / 2,
                left: trigged.left + trigged.width + triangle / 2 - 10 / 2 + 3,
                transform: 'rotate(135deg)'
            }
        }

        case 'RB': {
            return {
                top: trigged.top + trigged.height / 2 - triangle / 2,
                left: trigged.left + trigged.width + triangle / 2 - 10 / 2 + 3,
                transform: 'rotate(135deg)'
            }
        }

    }
}


export default {};
